import axios from "axios";

class Post {
  getteams() {
    return axios.get("http://localhost:8001/allteams");
  }

  getProduts() {
    return axios.get("http://localhost:8001/allitems");
  }

  getReviews() {
    return axios.get("http://localhost:8001/allreviews");
  }
}

export default new Post();
